
import { defineComponent, PropType } from 'vue';

interface IList {
    imgUrl: string;
    title: string;
    subTitle: string;
    href?: string;
}

export default defineComponent({
    name: 'List',
    props: {
        data: {
            type: Array as PropType<Array<IList>>,
            default: () => [],
        },
    },
    setup() {
        return {};
    },
});
