
import {
    defineComponent,
    nextTick,
    ref,
    watch,
    onBeforeUpdate,
    onMounted,
    reactive,
    Ref,
} from 'vue';
import { useStore } from '@/store/index';
import { Store } from 'vuex';
import { IState } from '@/global';
import {
    useRoute,
    useRouter,
    Router,
    RouteLocationNormalizedLoaded,
} from 'vue-router';
import { ITagsList } from '@/type/store/layout';

// 右键菜单
const rightMenu = (
    store: Store<IState>,
    router: Router,
    route: RouteLocationNormalizedLoaded
) => {
    const menuPos = reactive({
        left: '0px',
        top: '0px',
        display: 'none',
    });
    const rightMenuEl: Ref<HTMLElement | null> = ref(null);
    // 当前右键的那个标签
    let currentRightTags: ITagsList;
    const contextRightMenu = (v: ITagsList, event: MouseEvent) => {
        currentRightTags = v;
        menuPos.display = 'block';
        nextTick(() => {
            let left = event.clientX - 5;
            if (!rightMenuEl.value) return;
            if (
                event.clientX + rightMenuEl.value.offsetWidth >
                document.body.offsetWidth
            ) {
                left = event.clientX - rightMenuEl.value.offsetWidth;
            }
            menuPos.left = `${left}px`;
            menuPos.top = `${event.clientY + 10}px`;
        });
    };
    const refresh = () => {
        if (currentRightTags.path === route.path) {
            router.replace(`/redirect${currentRightTags.path}`);
        } else {
            router.push(`/redirect${currentRightTags.path}`);
        }
    };
    const closeOther = () =>
        store.commit('layout/removeOtherTagNav', currentRightTags);
    document.body.addEventListener('click', () => (menuPos.display = 'none'));
    return { menuPos, contextRightMenu, refresh, rightMenuEl, closeOther };
};

// 标签页滚动
const tagScroll = (store: Store<IState>) => {
    const { tagsList, cachedViews } = store.state.layout.tags;
    const scrollbar: Ref<{ wrap: HTMLElement; update(): void } | null> = ref(
        null
    );
    const layoutTagsItem: Ref<Array<HTMLElement>> = ref([]);
    // 监听标签页导航
    watch(
        () => tagsList.length,
        () =>
            nextTick(() => {
                if (!scrollbar.value) return;
                scrollbar.value.update();
                nextTick(() => {
                    const itemWidth = layoutTagsItem.value
                        .filter((v) => v)
                        .reduce((acc, v) => {
                            return acc + v.offsetWidth + 6;
                        }, 0);
                    if (!scrollbar.value) return;
                    const scrollLeft =
                        itemWidth - scrollbar.value.wrap.offsetWidth + 70;
                    if (scrollLeft > 0)
                        scrollbar.value.wrap.scrollLeft = scrollLeft;
                });
            })
    );
    // 确保在每次变更之前重置引用
    onBeforeUpdate(() => {
        layoutTagsItem.value = [];
    });
    return { tagsList, scrollbar, layoutTagsItem, cachedViews };
};
export default defineComponent({
    name: 'LayoutTags',
    setup() {
        const store = useStore();
        const route = useRoute();
        const router = useRouter();
        const removeTagNav = (v: any) =>
            store.commit('layout/removeTagNav', {
                cPath: route.path,
                tagsList: v,
            });
        const closeAll = () => store.commit('layout/removeAllTagNav');

        onMounted(() => {
            store.commit('layout/addCachedViews', {
                name: route.name,
                noCache: route.meta.noCache,
            });
        });

        const rightMenuData = rightMenu(store, router, route);
        const tagScrollData = tagScroll(store);

        return {
            removeTagNav,
            ...tagScrollData,
            ...rightMenuData,
            closeAll,
        };
    },
});
